













import Vue from "vue";

export default Vue.extend({
    props: {
        name: String, 
        initialEnthusiasm: Number,
        DataObject: {
            type: Object as () => AGVStateCount,
            default : {
                id: "?",
                title: "Unknown",
                icon:"",
                color:"#676c71",
                value: 0,
                count: 0,
            }
        }
    }
});
export interface AGVStateCount {
    id: string,
    title: string,
    icon:string,
    color:string,
    value: number,
    count: number
}
